import { Container, Grid } from "@mui/material";
import ResultTitle from "../components/Result/ResultTitle";
import Graph from "../components/Result/Graph";
import { useLocation } from "react-router-dom";

const calculate = (input, result) => {
  let temperature = result;

  if (parseFloat(input.currentIn > input.schedule)) {
    temperature =
      temperature +
      (12 *
        parseFloat(input.size) *
        (parseInt(input.currentOut) - parseInt(input.currentIn)) -
        500) /
        (parseInt(input.mass) * 3900);

    return temperature;
  } else {
    temperature =
      temperature +
      (12 * input.size * (input.currentOut - input.currentIn)) /
        (input.mass * 3900);

    return temperature;
  }
};

export default function Result() {
  const location = useLocation();

  const input = { ...location.state.data };

  const startHourTime = parseInt(input.startTime.split(":")[0]);
  const startMinTime = parseInt(input.startTime.split(":")[1]);
  const endHourTime = parseInt(input.endTime.split(":")[0]);
  const endMinTime = parseInt(input.endTime.split(":")[1]);

  const temperatureData = [];
  const mins = (endHourTime - startHourTime) * 60 + (endMinTime - startMinTime);

  let temp = input.currentIn;

  for (let i = 0; i <= mins; i++) {
    let timestamp = startHourTime * 60 + startMinTime;
    timestamp += i;

    temp = calculate(input, temp);

    temperatureData.push({
      hour: parseInt(timestamp / 60),
      minute: timestamp % 60,
      // timestamp: timestamp,
      temperature: temp,
    });
  }

  const xData = temperatureData.map(
    (data) => data.hour.toString() + ":" + data.minute.toString()
  );
  const yData = temperatureData.map((data) => parseFloat(data.temperature));

  return (
    <>
      <Container
        maxWidth="lg"
        sx={{
          display: "flex",
          flexDirection: "column",
          mt: "calc(3vh)",
        }}
      >
        <ResultTitle />
      </Container>
      <Container>
        <Grid container rowSpacing={1} columnSpacing={{ xs: 1, sm: 1, md: 10 }}>
          <Grid item xs={13} sm={6}>
            <Graph title={"냉장고 내부 온도"} xData={xData} yData={yData} />
          </Grid>
          <Grid item xs={13} sm={6}>
            <Graph
              title={"냉장고 설정 온도"}
              xData={[
                "13:00",
                "13:01",
                "13:02",
                "13:03",
                "13:04",
                "13:05",
                "13:06",
              ]}
              yData={[5, 5, 5, 6, 6, 3, 3]}
            />
          </Grid>
          <Grid item xs={13} sm={6}>
            <Graph
              title={"전력사용량"}
              xData={[
                "13:00",
                "13:01",
                "13:02",
                "13:03",
                "13:04",
                "13:05",
                "13:06",
              ]}
              yData={[270, 468, 540, 235, 564, 213, 684]}
            />
          </Grid>
          <Grid item xs={13} sm={6}>
            <Graph
              title={"전기요금"}
              xData={[
                "13:00",
                "13:01",
                "13:02",
                "13:03",
                "13:04",
                "13:05",
                "13:06",
              ]}
              yData={[1900, 3200, 4500, 3200, 3400, 5200, 4100]}
            />
          </Grid>
        </Grid>
      </Container>
    </>
  );
}
