import {
  Box,
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  MenuItem,
  TextField,
} from "@mui/material";
import { useState } from "react";
import { Controller, useForm } from "react-hook-form";
import { useNavigate } from "react-router-dom";

export default function InputDialog({ open, handleClose }) {
  const navigate = useNavigate();
  const { register, handleSubmit, control } = useForm();

  const [formData, setFormData] = useState([
    { id: 0, value: { start: "", end: "", tem: 0 } },
  ]);
  const [nextId, setNextId] = useState(1);

  const handleAddInput = (data) => {
    setFormData([...formData, { id: nextId, value: data }]);
    setNextId(nextId + 1);
  };

  const handleChange = (id, event) => {
    const newFormData = formData.map((data) =>
      data.id === id ? { ...data, value: event.target.value } : data
    );
    setFormData(newFormData);
  };

  const handleRemoveInput = (id) => {
    setFormData(formData.filter((data) => data.id !== id));
  };

  const seasonOption = [
    {
      value: "0",
      label: "봄",
    },
    {
      value: "1",
      label: "여름",
    },
    {
      value: "2",
      label: "가을",
    },
    {
      value: "3",
      label: "겨울",
    },
  ];

  return (
    <>
      <Dialog
        open={open}
        onClose={handleClose}
        PaperProps={{ sx: { borderRadius: "15px", width: "calc(70vw)" } }}
      >
        <form
          onSubmit={handleSubmit((data) =>
            navigate("/result", {
              state: {
                data,
              },
            })
          )}
        >
          <DialogTitle>User Input Info</DialogTitle>
          <DialogContent>
            <TextField
              margin="dense"
              id="size"
              label="냉장고 표면적"
              fullWidth
              variant="standard"
              {...register("size")}
            />
            <TextField
              margin="dense"
              id="mass"
              label="냉장고 내부 총 질량"
              fullWidth
              variant="standard"
              {...register("mass")}
            />
            <TextField
              margin="dense"
              select
              id="season"
              label="계절"
              fullWidth
              defaultValue={0}
              variant="standard"
              {...register("season")}
            >
              {seasonOption.map((option) => (
                <MenuItem key={option.value} value={option.value}>
                  {option.label}
                </MenuItem>
              ))}
            </TextField>
            <TextField
              type="time"
              margin="dense"
              id="startTime"
              label="그래프 시작 시간"
              fullWidth
              variant="standard"
              InputLabelProps={{ shrink: true }}
              {...register("startTime")}
            />
            <TextField
              type="time"
              margin="dense"
              id="endTime"
              label="그래프 끝 시간"
              fullWidth
              variant="standard"
              InputLabelProps={{ shrink: true }}
              {...register("endTime")}
            />
            {formData.map((data) => (
              <div key={data.id}>
                <Controller
                  control={control}
                  name={`field_${data.id}`}
                  defaultValue=""
                  render={({ field }) => (
                    <Box
                      sx={{ display: "flex", alignItems: "flex-end" }}
                      gap={1}
                    >
                      <TextField
                        {...field}
                        margin="dense"
                        variant="standard"
                        fullWidth
                        value={typeof data.value === "string" ? data.value : ""}
                        placeholder={`스케줄`}
                        InputLabelProps={{ shrink: true }}
                        onChange={(event) => handleChange(data.id, event)}
                      />
                      <Button
                        color="warning"
                        size="small"
                        type="button"
                        onClick={() => handleRemoveInput(data.id)}
                      >
                        삭제
                      </Button>
                    </Box>
                  )}
                />
              </div>
            ))}
            <Button variant="outlined" onClick={handleAddInput}>
              + 스케줄 추가
            </Button>
            <TextField
              margin="dense"
              id="currentIn"
              label="냉장고 현재 내부온도 (℃)"
              fullWidth
              variant="standard"
              {...register("currentIn")}
            />
            <TextField
              margin="dense"
              id="currentOut"
              label="냉장고 현재 외부온도 (℃)"
              fullWidth
              variant="standard"
              {...register("currentOut")}
            />
          </DialogContent>
          <DialogActions>
            <Button onClick={handleClose}>취소</Button>
            {/* <Link to={"/result"}> */}
            <Button type="submit">그리기</Button>
            {/* </Link> */}
          </DialogActions>
        </form>
      </Dialog>
    </>
  );
}
